.wp-block-blocklayouts-icon {
	display: flex;

	&.justify-center {
		justify-content: center;
	}
	&.justify-right {
		justify-content: flex-end;
	}
	&.justify-left {
		justify-content: flex-start;
	}

	.icon-container {
		line-height: 0;
	}

	svg {
		height: 100%;
		width: 100%;
	}

	&.is-fill svg {
		fill: currentColor;
	}

	&.is-line svg path {
		stroke: currentColor;
	}
}
